.btn-checkbox {
  background-color: #fff;
  border-radius: 3px;
  overflow: hidden;
  margin-right: 6px;
}

.btn-checkbox:last-child {
  margin-right: 0;
}

.btn-checkbox label {
  width: 100%;
  margin: 0;
  position: relative;
}

.btn-checkbox label span {
  text-align: center;
  display: flex;
  align-items: center;
  transition: 0.3s ease;
  padding: 10px 18px;
  font-weight: 500;
  font-size: 14px;
  border: 1px solid #e3e6ef;
}

.btn-checkbox label span i {
  font-size: 16px;
  margin-right: 5px;
  margin-top: -1px;
  width: auto;
  height: auto;
  top: 0;
}

.btn-checkbox label input {
  position: absolute;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
  outline: 0;
  background: transparent !important;
}

.btn-checkbox label input:focus {
  outline: 0;
}

.btn-checkbox label input:checked:before {
  content: none !important;
}

.btn-checkbox label input:checked + span {
  color: #fff !important;
  background: #444752;
  border-color: #444752;
}

